.listivo_about_welcome {
	margin-bottom: rem(130);

	@include min {
		margin-bottom: rem(100);
	}
	
	.container-min {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
	}

	&-image, &-text {
		flex: 0 0 47%;

		@include min {
			flex: 0 0 48%;
		}

		@include min660 {
			flex: 0 0 100%;
		}
	}

	&-image {
		overflow: hidden;
		border-radius: rem(10);
		max-height: rem(425);
		margin-right: rem(30);

		@include min {
			margin-right: rem(15);
		}

		@include min660 {
			margin-right: 0;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	&-text {
		margin-left: rem(30);

		@include min {
			margin-left: rem(15);
		}

		@include min660 {
			margin-left: 0;
		}
		
		.listivo_title {
			position: relative;
			font-size: rem(36);
			line-height: rem(46);
			font-weight: 700;
			font-family: $font-title;
			margin-bottom: rem(30);

			@include min {
				font-size: rem(28);
				line-height: rem(38);
				margin-bottom: rem(20);
			}

			@include min660 {
				margin-top: rem(16);
			}

			@include min540 {
				font-size: rem(24);
				line-height: rem(34);
			}

			&::after {
				content: "";
				width: rem(117);
				height: rem(6);
				display: block;
				background: url('data:img/png;base64,') no-repeat;
				background-size: 100%;
				margin-top: rem(3);
			}
		}

		b {
			font-size: rem(18);
			line-height: rem(26);
			font-weight: 500;
			display: block;
			margin-bottom: rem(26);

			@include min {
				margin-bottom: rem(16);
			}
		}

		p {
			line-height: rem(26);
			color: var(--text-descr);
			margin-bottom: rem(46);

			@include min {
				margin-bottom: rem(26);
			}
		}
	}
}