.listivo_header_search {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	height: rem(410);

	
	margin-bottom: rem(120);

	@include min {
		margin-bottom: rem(80);
	}

	@include min540 {
		height: rem(500);
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: url(../img/header_search_bg.png) no-repeat top center;
		background-size: cover;
		z-index: -2;
	}

	.listivo_title {
		position: relative;
		font-size: rem(48);
		line-height: rem(76);
		font-weight: 700;
		font-family: 'Comfortaa';
		display: block;
		margin-bottom: rem(26);

		@include min {
			font-size: rem(38);
			line-height: rem(66);
		}

		@include min660 {
			margin-top: rem(80);
			font-size: rem(24);
			line-height: rem(46);
		}

		&::after {
			content: "";
			position: absolute;
			top: rem(56);
			right: rem(240);
			width: rem(284);
			height: rem(13);
			background: url(../img/line4.png) no-repeat;
			z-index: -1;

			@include tablet {
				width: rem(290);
				right: rem(245);
			}

			@include min {
				width: rem(172);
				height: rem(8);
				
				top: rem(48);
				right: rem(272);
			}

			@include min660 {
				display: none;
			}
		}

		&::before {
			content: "";
			position: absolute;
			bottom: rem(15);
			right: rem(-75);
			width: rem(55);
			height: rem(41);
			background: url(../img/arrow_blue.png) no-repeat;
			background-size: 100%;
			transform: rotate(180deg);

			@include min540 {
				display: none;
			}
		}
	}

	.listivo_search_form {
		position: relative;
		width: rem(1000);

		@include min {
			width: 90%;
			flex-wrap: nowrap;
		}

		@include min660 {
			margin-top: rem(20);
			margin-bottom: auto;
			flex-wrap: wrap;
		}

		@include hd {
			// width: rem(1280);
		}

		&-field {

			input {

				&:last-child {
					width: 100%;
					max-width: rem(250);

					@include tablet {
						max-width: rem(220);
					}

					@include min {
						max-width: 100%;
					}
				}
			}
		}
	}
}