.listivo_article_page {

	border: rem(1) solid #e4ebf0;
	border-radius: rem(10);
	padding-bottom: rem(58);
	margin-bottom: rem(92);

	@include min {
		margin-bottom: rem(62);
		padding-bottom: rem(32);
	}

	&-preview {
		width: 100%;
		border-radius: rem(10);
		overflow: hidden;
		margin-bottom: rem(20);
	}

	&-px {
		padding: 0 rem(45);

		@include min {
			padding: 0 rem(25);
		}
	}
	
	.listivo_author {
		width: 100%;
	}

	h1 {
		font-size: rem(36);
		font-weight: 700;
		font-family: $font-title;
		margin-bottom: rem(32);

		@include min {
			font-size: rem(26);
			margin-bottom: rem(20);
		}
	}

	h2 {
		font-size: rem(24);
		font-weight: 700;
		font-family: $font-title;
		margin-bottom: rem(20);

		@include min {
			font-size: rem(20);
			margin-bottom: rem(16);
		}
	}

	.listivo_text  {
		line-height: rem(28);
		color: var(--text-descr);
		margin-bottom: rem(32);

		@include min {
			margin-bottom: rem(16);
		}
	}

	blockquote {
		position: relative;
		height: rem(245);
		background-color: var(--color2);
		display: flex;
		flex-direction: column;
		justify-content: center;
		padding-right: rem(70);
		padding-left: rem(110);
		overflow: hidden;
		margin-bottom: rem(52);

		@include min {
			padding-right: rem(50);
			padding-left: rem(90);
			margin-bottom: rem(42);
			height: rem(225);
		}
		
		svg {
			position: absolute;
			right: rem(42);
			bottom: rem(-24);
		}
		// align-items: center;

		p {
			font-size: rem(24);
			line-height: rem(32);
			color: white;
			font-weight: 700;
			font-family: $font-title;
			margin-bottom: rem(20);

			@include min {
				font-size: rem(20);
				line-height: rem(28);
				margin-bottom: rem(16);
			}
		}

		span {
			font-size: rem(18);
			font-weight: 700;
			font-family: $font-title;
			color: rgba(255,255,255,.5);
		}
	}

	&-images {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin: rem(58) 0;

		@include min768 {
			margin: rem(32) 0;
		}

		img {
			@include min {
				width: calc((100% - 1.25rem) / 2);
				margin-bottom: rem(20);
			}
		}
	}

	&-footer {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;
	}
}

.listivo_section {
	margin-bottom: rem(92);

	@include min {
		margin-bottom: rem(60);
	}

	> .listivo_title {
		font-size: rem(36);
		font-weight: 700;
		font-family: $font-title;
		line-height: rem(48);
		margin-bottom: rem(42);

		@include min {
			font-size: rem(30);
			line-height: rem(42);
			margin-bottom: rem(32);
		}
	}

	@include min {
		.listivo_item {
			width: calc((100% - 1.25rem) / 2);
		}
	}

	@include min540 {
		.listivo_item {
			width: 100%;
		}
	}
}

.listivo_comment {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: rem(32);

	&:last-of-type {
		margin-bottom: 0;
	}

	&-image {
		flex: 0 0 rem(70);
		margin-right: rem(20);
		width: rem(70);
		height: rem(70);
		border-radius: 50%;
		
	}

	&-info {
		flex: 1 0 0;
		background-color: #f2fafc;
		padding: rem(58) rem(48) rem(51) rem(48);
		border-radius: rem(10);

		@include min {
			padding: rem(38) rem(28) rem(31) rem(28);
		}

		&_header {
			display: flex;
			flex-wrap: wrap;
			justify-content: space-between;

			margin-bottom: rem(32);

			@include min {
				margin-bottom: rem(16);

				> .flex {
					flex-direction: column;
					align-items: flex-start !important;
				}
			}

			.listivo_title {
				font-size: rem(20);
				font-weight: 700;
				font-family: $font-title;
				margin-right: rem(10);

				@include min {
					font-size: rem(18);
					margin-right: 0;
					display: block;
				}
			}

			.listivo_date {
				font-size: rem(12);
				font-weight: 600;
				color: var(--text-descr);

				@include min {
					display: block;
				}
			}
		}

		.listivo_text {
			font-size: rem(18);
			line-height: rem(28);
			color: var(--text-descr);
		}
	}
}